import gql from 'graphql-tag';
import ApolloClient, { createNetworkInterface, addTypename } from 'apollo-client';
	
(function(root,factory,includeTypes){ return factory(root,includeTypes) })(
	window,
	function factory(window,includes){
		
		const apollo = Symbol.for('Apollo Client')
		const interface_options = Symbol.for('Interface Options')
		const tagger = includes.gql
		
		
		class Interface{
			constructor(options){
				
				let batching = 'batching' in options ? options.batching : true
				let host = `${window.location.protocol}//${window.location.host}`
				let web_address = [host].concat(options.webpaths).join('/')
				
				this.networkInteface = includes.createNetworkInterface({
					uri:web_address,
					transportBatching:batching
				})
				
				this.queryTransformer = includes.addTypename
				
			}
		}
		
		class Client{
			
			static tag(value){ return this.tagger`${value}` }
			static get tagger(){ return tagger }
			
			get Interface(){ return Interface }
			
			constructor(options){
				if(typeof options !== 'object' || options === null) {
					options = {
						interface:{
							webpaths:['graph','ql']
						}
					}
				}
				this[interface_options] = new Interface(options.interface)

				
			}
			get interface(){
				if(apollo in this) return this[apollo]
				return this[apollo] = new includes.Apollo( this[interface_options] )
			}
		}
		
		if('Graph' in window) window.Graph.Client = Client
		else window.Graph = { Client }
		
		return window.Graph.Client
		
	},{ ApolloClient, addTypename, createNetworkInterface, gql });




